        <tou :linkTo="`/yunjian`"></tou>
        <div class="lefy">
              <router-view></router-view>
        </div>
        <div class="right_sub">
            <Button v-for="(item,index) in anNiuList" :class="{active_an:index==oneindex}" @click="relationClick(index,item.path)">{{item.name}}</Button>
            <Button class='tijiao'>提   交</Button>
        </div>
   </div>
</template>

<script>
    export default {
        name: "yunjian_detail",
        data(){
            return {
                anNiuList:[
                    {
                        name:'勘查',
                        path:'/yunjian/detail/kancha',
                    },
                    {
                        name:'设备状态',
                        path:'/yunjian/detail/shebeizhuangtai',
                    },
                    {
                        name:'定值整定',
                        path:'/yunjian/detail/dingzhizhengding',
                    },
                    {
                        name:'整定确定',
                        path:'/yunjian/detail/zhengdingqueren',
                    },
                    {
                        name:'措施票',
                        path:'/yunjian/detail/cuoshipiao',
                    },
                    {
                        name:'检修试验',
                        path:'/yunjian/detail/jianxiushiyan',
                    },
                    {
                        name:'工作票',
                        path:'/yunjian/detail/gongzuopiao',
                    }
                ],
                oneindex:0,
            }
        },
        methods:{
            relationClick(index,path){
                this.oneindex=index;
                this.$router.push(path);
                },
        }
    }
</script>

